import React from "react";
import { useTheme } from "next-themes";
import Link from "next/link";
import Image from "next/image";

interface ModuleCardProps {
  id: number;
  title: string;
  status: "completed" | "active" | "locked";
}

const ModuleCard: React.FC<ModuleCardProps> = ({ id, title, status }) => {
  const { theme } = useTheme();
  const moduleUrl = status === "locked" ? "#" : `/module/${id}`;

  return (
    <Link href={moduleUrl} passHref>
      <div
        className={`p-4 rounded-lg shadow-md border transition-transform duration-300 hover:scale-105 ${
          status === "locked" ? "cursor-not-allowed opacity-50" : "cursor-pointer"
        }`}
      >
        <span className="font-medium">{title}</span>
        <Image
          src={
            theme === "dark"
              ? "/assets/dark/lock.svg"
              : "/assets/light/lock.svg"
          }
          alt={status}
          width={24}
          height={24}
        />
      </div>
    </Link>
  );
};

export default ModuleCard;
